<template>
    <section class="search">
        <h1>Lookup Player Stats</h1>
        <form v-on:submit.prevent="onSubmit">
            <div class="form-group">
                <label for="playertag">PlayerTag:</label>
                <input 
                    type="text" 
                    name="text" 
                    v-model="playertag" 
                    id="playertag" 
                    placeholder="GVQ2VRL2"
                />
            </div>
            <div class="form-group">
                <input type="submit" value="Submit" class="btn">
            </div>
        </form>
        <div class="savedLinks">
            <h4>Saved links (hardcoded):</h4>
            <router-link to="/profile/GVQ2VRL2">clog</router-link>
            <router-link to="/profile/CCCLLC0P">Nooblicity</router-link>
            <router-link to="/profile/RCYYPU00">Yoo</router-link>
            <router-link to="/profile/UP9LYJVJ">Linear</router-link>
        </div>
        <div class="disclaimer">
            <p>For educational purposes only.</p>
        </div>
    </section>
</template>

<script>
export default {
    name: 'Search',
    data() {
        return {
            playertag: ""
        }
    },
    methods: {
        onSubmit() {
            if (!this.playertag) {
                this.$toasted.show("Please enter a player tag.", {
                    duration: 3000,
                    icon: 'exclamation-circle'
                });
            } else {
                // redirect to profile component
                this.$router.push(`/profile/${this.playertag}`)
            }
        }
    }
}
</script>

