        </div>
    </section>
</template>

<script>
import axios from 'axios';
export default {
    name: 'Profile',
    data() {
        return {
            loading: false,
            error: null,
            profileData: null
        }
    },
    async created() {
        this.loading = true;
        try {
            const res = await axios.get(`/api/v1/profile/${this.$route.params.playertag}`);

            this.profileData = res.data;
            console.log(this.profileData);

            this.loading = false;
        } catch (err) {
            this.loading = false;
            this.error = err.response.data.message;
        }
    }
}
</script>

